@use "base/variables" as v;
.alerta{
    padding: 1rem;
    text-transform: uppercase;
    font-weight: 900;
    margin-bottom: 1rem;
    font-size: 1.2rem;
    max-width: 80rem;

    &.error{
        background-color: lighten($color: v.$rojo, $amount: 60%);
        border-left: 1rem solid v.$rojo;
        color: v.$rojo;
    }
    &.exito{
        background-color: lighten($color: v.$verde, $amount: 60%);
        border-left: 1rem solid v.$verde;
        color: v.$verde;
    }
}